.h1DominiosTecnicos{
    font-family: 'Teko', sans-serif;
    font-weight: 400;
    font-size: calc(3vh + 5vw);
    position:relative;
    left:25vw;
    width: 50vw;
    border: 0px solid black;
}

.divDominiosTecnicos{
    font-family: 'Share Tech Mono', sans-serif;
    font-size:10vw;
    width:70%;
    position:relative;
    left:19vw;
    border:0px solid black;
    opacity: 0; /* Inicialmente define a opacidade do elemento para 0 */
    transition: opacity 1s ease-in-out;
}
.divDominiosTecnicos.divDominiosTecnicosVisivel{
    opacity:1;
}

.marcaVerificacao{
    width:calc(6vw + 3vh);
}

